class ManifestsLoader {
  public static manifestObject: {} = {};

  public static pitch(remainingRequest: string): string {
    return `module.exports = ${JSON.stringify(ManifestsLoader.manifestObject, undefined, 2)};`;
  }
}

/* tslint:disable:export-name */
export = ManifestsLoader;
/* tslint:enable:export-name */
